.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  text-align: center;
  letter-spacing: -1.5px;
  text-transform: capitalize;
  color: $colorText;

  @include onTablet {
    font-size: 48px;
    line-height: 100%;
  }

  @include onDesktop {
    font-size: 64px;
    line-height: 100%;
  }

  &--align--left {
    text-align: left;
  }
}
